import "./prject.css";
import Dialog from "@material-ui/core/Dialog";
import React from "react";
import { DialogContent, DialogTitle, DialogActions } from "@material-ui/core";
import Button from '@material-ui/core/Button';

export default function Prject() {
  const [selectedProject, setSelectedProject] = React.useState(false);
  const handleopen = (p) => {
    setSelectedProject(p);
  };
  const handleClose = () => {
    setSelectedProject(false);
  };

  const projectsData = [
      {
        dname: "Computer Vision Based Athletes Analyzer",
        dinfo: "information about project",
      },
      {
        dname: "Advanced Technology Store",
        dinfo: "information about project",
      },
    {
      dname: "Student Management System",
      dinfo: "information about project",
    },
    {
      dname: "IPL Data Analysis",
      dinfo: "information about project",
    },
    {
      dname: "Stock Market Analysis",
      dinfo: "information about project",
    },
    {
      dname: "Weather forecasting with beautiful soup",
      dinfo: "information about project",
    },
  ];

  return (
    <div>
      <h2>My Projects</h2>
      <div id="dprbox">
        {projectsData.map((project) => {
          return (
            <div class="dprjbox" onClick={() => handleopen(project)}>
              <h3>{project.dname}</h3>
            </div>
          );
        })}
      </div>
      <Dialog
        onClose={handleClose}
        aria-labelledby="simple-dialog-title"
        open={selectedProject}
      >
        <DialogTitle>{selectedProject.dname}</DialogTitle>
        <DialogContent>
            <p>{selectedProject.dinfo}</p>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
