import "./certi.css";
import PythonWebinar from "./PythonWebinar.png";
import AndroidWebinar from "./AndroidWebinar.png";
export default function Certi() {
  return (
    <div class="dcerti">
      <div id="dtitle">
        <h2>Certificates</h2>
      </div>
      <div id="dbox">
        <div id="dbox1" class="dpbox">
          <img src={PythonWebinar}></img>
        </div>
        <div id="dbox2" class="dpbox">
          <img src={AndroidWebinar}></img>
        </div>
      </div>
    </div>
  );
}